//1
import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/components/Home.vue'
import City from '@/components/Home/City/City.vue'
//2
Vue.use(VueRouter)


//3
const routes = [
  {
  path: '/',
  name:Home,
  component: Home
},
  {
    path: '/City',
    name: City,
    component: City
  },
]

//4
const router = new VueRouter({
  routes
})
//5
export default router


