import CiHeader from './page/Header'
import CiList from './page/list'
export default {
    name:'Header',
    components:{    
        CiHeader,
        CiList
    },
    data(){
        return{
            hotCities:[],
            cities:[]
        }
    },
    // axios   
     mounted(){
      this.$http.get("/api/city.json").then((res)=>{
        // console.log(res.data.data);
        const data= res.data.data;
        // 
        this.hotCities=data.hotCities;
        // 
        this.cities=data.cities;
      })

    }
}



